  font-family: 'Roboto';
  src: url('./fonts/roboto/Roboto-Regular.ttf') format('ttf');
  font-weight: 400; /* Товщина 400 (Regular) */
}
@font-face {
  font-family: 'Roboto';
  src: url('./fonts/roboto/Roboto-Medium.ttf') format('ttf');
  font-weight: 500; /* Товщина 500 (Medium) */
}
